import { getTopBooks, getBooksByCategory } from './booksAPI.js';
import { spinnerPlay, spinnerStop } from './spinner.js';
 import { onOpenModal } from './modal.js';
import { showNoBooksAlert } from './warnings.js';

const references = {
  topListElem: document.querySelector('#topList'),
  categoryListElem: document.querySelector('#categoryList'),
  titleElement: document.querySelector('.top_list-title'),
  allCategoriesElement: document.querySelector('#categories'),
};

let noBooksAlertShow = false;

document.addEventListener('DOMContentLoaded', function () {
  document.addEventListener('click', clickSeeMore); //See More butonuna tÄ±klanÄ±nca ilgili kategoriye ait kitaplarÄ± gÃ¶sterir.
  document.addEventListener('click', clickByBook); //Bir kitap kartÄ±na tÄ±klanÄ±nca detaylarÄ±nÄ± gÃ¶steren modal aÃ§ar.
  document.addEventListener('click', clickByAllCategories); //Bir kategoriye tÄ±klanÄ±nca bu kategorinin kitaplarÄ± gÃ¶sterir.
  window.addEventListener('scroll', scrollHandler); //SayfanÄ±n en altÄ±na kaydÄ±rÄ±ldÄ±ÄŸÄ±nda kategorinin boÅŸ olup olmadÄ±ÄŸÄ±nÄ± kontrol eder.
});

//En Ã‡ok Satanlar Listesini Getirme
document.addEventListener('DOMContentLoaded', async function () {
  spinnerPlay(references.titleElement);
  references.topListElem.innerHTML = '';
  const topListData = getTopBooks();
  const topList = await topListData;
  let html = '';
  topList.forEach(elem => {
    html += renderTopList(elem);
  });
  references.topListElem.innerHTML = html;
  spinnerStop();
});

//En Ã‡ok Satanlar Listesini Renderleme
function renderTopList(elem) {
  return `<li class="top_list-container">
            <h2 class="top_list-category_name">${elem.list_name}</h2>
            <ul class="top_list-cards">${renderBooks(elem.books)}</ul>
            <button class="top_list-see_more" data-category="${
              elem.list_name
            }">See More</button>
        </li>`;
}

//Kitap kartlarÄ±nÄ±n oluÅŸturulmasÄ± (kapak, baÅŸlÄ±k ve yazar ismi)
function renderBooks(books) {
  let i = 1;
  const bookHtml = books
    .map(
      book => `
        <li class="top_list-card" data-book-sequence-number="${i++}">
            <div class="top_list-book_cover_wrapper" data-bookid="${book._id}">
                <img class="top_list-book_cover" src="${
                  book.book_image
                }" alt="${book.title}">
            <p class="top_list-quick">QUICK VIEW</p>
                </div>
            
            <h3 class="top_list-book_title">${book.title}</h3>
            <p class="top_list-book_author">${book.author}</p>
        </li>
        `
    )
    .join('');
  i++;
  return bookHtml;
}

const clickByBook = function (event) {
  const bookWrapper = event.target.closest('.top_list-book_cover_wrapper');
  if (bookWrapper) {
    const bookId = bookWrapper.dataset.bookid;
    onOpenModal(bookId); //Kitap ID' si ile birlikte modal aÃ§ma fonksiyonunu Ã§alÄ±ÅŸtÄ±rÄ±r.
  }
};

const clickSeeMore = function (event) {
  if (event.target.classList.contains('top_list-see_more')) {
    references.topListElem.classList.add('hidden'); //Ãœst kÄ±sÄ±mdaki kitap listesi gizlenir.
    references.categoryListElem.classList.remove('hidden'); //Kategorilerin bulunduÄŸu liste alanÄ± gÃ¶sterilir.
    const category = event.target.dataset.category;
    titleCategory(category); //SeÃ§ilen kategorinin baÅŸlÄ±ÄŸÄ±nÄ± deÄŸiÅŸtirir.
    categoryList(category); //SeÃ§ilen kategoriye ait kitaplarÄ± getirip listeler.
    allCategoriesActive(category); //SeÃ§ilen kategoriyi aktif hale getirir. GÃ¶rsel vurgulama ekler.
  }
};

const clickByAllCategories = async function (event) {
  if (event.target.classList.contains('all_categories')) { //All Kategories butonuna tÄ±klandÄ±ÄŸÄ±nda;
    references.titleElement.innerHTML = 'Best Sellers <span>Books</span>';
    references.categoryListElem.classList.add('hidden');
    references.topListElem.classList.remove('hidden');
    allCategoriesActive('All categories');
  }
  if (event.target.classList.contains('...')) { //Kategori listesine (baÄŸlantÄ±sÄ±na) tÄ±klandÄ±ÄŸÄ±nda;
    references.titleElement.innerHTML = category; //BaÅŸlÄ±ÄŸÄ± kategori adÄ± ile deÄŸiÅŸtirir.
    references.categoryListElem.classList.remove('hidden');
    references.topListElem.classList.add('hidden');
    allCategoriesActive(category);
    await categoryList(category); //SeÃ§ilen kategoriye ait kitaplarÄ± getirir.
  }
};

//Kategoriye Ait KitaplarÄ±n Listesini Getirme
async function categoryList(category) {
  try {
    spinnerPlay(references.titleElement);
    references.categoryListElem.innerHTML = '';

    const categoryData = await getBooksByCategory(category);
    let html = '';

    categoryData.forEach(book => {
      html += `
          <li class="category_list-card">
              <div class="top_list-book_cover_wrapper" data-bookid="${book._id}">
                  <img class="top_list-book_cover" src="${book.book_image}" alt="${book.title}">
              </div>
              <h3 class="top_list-book_title">${book.title}</h3>
              <p class="top_list-book_author">${book.author}</p>
          </li>
          `;
    });

    html += `<li><button class="all_categories">All Categories</button></li>`;
    references.categoryListElem.innerHTML = html;
  } catch (error) {
    console.error('Kategori verisi alÄ±nÄ±rken hata oluÅŸtu:', error);
    references.categoryListElem.innerHTML =
      '<p>Kitaplar yÃ¼klenirken bir hata oluÅŸtu.</p>';
  } finally {
    spinnerStop();
  }
}

//SeÃ§ilen kategori adÄ±nÄ± sayfa baÅŸlÄ±ÄŸÄ± olarak gÃ¶sterir.
function titleCategory(category) {
  references.titleElement.innerHTML = '';

  const words = category.split(' ');
  if (words.length === 1) {
    references.titleElement.textContent = category;
    return;
  }

  const lastWord = words.pop();
  references.titleElement.textContent = words.join(' ');

  const spanElement = document.createElement('span');
  spanElement.textContent = ' ' + lastWord;
  references.titleElement.appendChild(spanElement);
}

//SeÃ§ilen kategori aktif olur.
function allCategoriesActive(category) {
  document.querySelectorAll('...'); // TÃ¼m kategori baÄŸlantÄ±larÄ±nÄ± seÃ§er.

  references.allCategoriesElement.forEach(link => {
    if (link.textContent.trim() === category) {
      link.classList.add('active');
    } else {
      link.classList.remove('active');
    }
  });
}

//Sayfa en alta kaydÄ±rÄ±ldÄ±ÄŸÄ±nda seÃ§ilen kategoriye ait kitap yoksa bu kategoride kitap bulunmamaktadÄ±r uyarÄ±sÄ± verir.
function scrollHandler() {
  const allCategoriesButton = document.querySelector('.all_categories');
  if (allCategoriesButton) {
    const allCatBtnPosition = allCategoriesButton.getBoundingClientRect(); //Butonun konumu alÄ±nÄ±r.
    const windowHeight = window.innerHeight; //KullanÄ±cÄ±nÄ±n gÃ¶rÃ¼nÃ¼r pencere yÃ¼kseliÄŸini alÄ±r.

    if (
      allCatBtnPosition.top < windowHeight &&
      !noBooksAlertShow &&
      references.topListElem.classList.contains('hidden')
    ) {
      showNoBooksAlert();
      noBooksAlertShow = true;
    }
  }
}

//SayfanÄ±n en alta gelip gelmediÄŸini kontrol eder ve uyarÄ± mesajÄ± goÌˆsterir.
let scrollTimeout;

window.addEventListener('scroll', function () {
  if (scrollTimeout) {
    clearTimeout(scrollTimeout);
  }

  scrollTimeout = setTimeout(function () {
    scrollHandler();
  }, 300);
});
